function input(props){

    return (
        <div>
            <textarea
                id='editor'
                value={props.input}
                onChange={props.change}
            >What <br /> -- <br /> manner <br /> == <br /> of *madness* **is** this <br /> what <br /> ___</textarea>
            <style jsx>{`
                #editor {
                    width: 800px;
                    height: 200px;
                    margin: auto;
                } 
            `}</style>
        </div>
    )
}

export default input;